import React from 'react';

import './FeedbackForm.css'

const FeedbackForm = () => {

    const handleSubmit = (e) => {
        e.preventDefault();
    }

    return (
        <form
            className={'feedback__form'}
            onSubmit={(e) => handleSubmit(e)}
        >
            <input type="text" placeholder={''}/>
            <input type="text" placeholder={' '}/>
            <textarea placeholder={''}/>
            <button></button>
        </form>
    );
};

export default FeedbackForm;