import React from 'react';
import FeedbackForm from "./FeedbackForm/FeedbackForm";

import './Feedback.css'

const Feedback = () => {
    return (
        <section id={'feedback'}>
            <div className="container">
                <div className="row">
                    <div className="col-2 feedback__col-left">
                        <p>    <br/>  </p>
                        <p>      <br/>       <br/>  . </p>
                    </div>
                    <div className="col-2">
                        <FeedbackForm/>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default Feedback;