import React from 'react';
import {Link} from "react-router-dom";

const FooterNav = () => {
    return (
        <div className={'footer__nav'}>
            <p className={'footer__nav-title'}></p>
            <div className="footer__nav-items">
                <Link to={'/'}></Link>
                <Link to={'/about'}> </Link>
                <Link to={'/services'}></Link>
                <Link to={'/blog'}></Link>
            </div>
        </div>
    );
};

export default FooterNav;