import React from 'react';
import {Swiper, SwiperSlide} from "swiper/react";
import {Keyboard, Mousewheel, Pagination} from "swiper";
import img1 from "../../../assets/images/slider/img1.png";
import img2 from "../../../assets/images/slider/img2.png";

import './CoverSlider.css'

const TestSlider = () => {
    return (
        <div className={'slider pt-100px'}>
            <Swiper
                pagination={{
                    clickable: true,
                    dynamicBullets: true,
                }}
                mousewheel={false}
                keyboard={true}
                autoplay={{
                    delay: 4500,
                    disableOnInteraction: false,
                }}
                modules={[ Pagination, Mousewheel, Keyboard]}
                className="mySwiper"

            >
                <SwiperSlide>
                    <div
                        className={'slider-item'}
                        style={{backgroundImage: `url(${img1})`}}
                    >
                        <div className="slider-item__box">
                            <p><span>   <br/>
                                 </span>     <br/>
                                 , ,  <br/>
                                     <br/>
                                .
                            </p>
                            <button></button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div
                        className={'slider-item-second'}
                        style={{backgroundImage: `url(${img2})`}}
                    >
                        <div className={'slider-item__box-second'}>
                            <h2>      </h2>
                            <p>         </p>
                        </div>
                        <div className={'slider__btn-second'}>
                            <button></button>
                        </div>
                    </div>
                </SwiperSlide>
                <SwiperSlide>
                    <div
                        className={'slider-item'}
                        style={{backgroundImage: `url(${img1})`}}
                    >
                        <div className="slider-item__box">
                            <p>
                                <span>
                                       <br/>
                                     
                                </span>
                                    <br/>
                                 , ,  <br/>
                                     <br/>
                                .
                            </p>
                            <button></button>
                        </div>
                    </div>
                </SwiperSlide>
            </Swiper>
        </div>
    );
};

export default TestSlider;