import React from 'react';

import img from '../../../assets/images/Rectangle.png';

import './AboutFeed.css'

const AboutFeed = () => {
    return (
        <section id={'about-feed'}>
            <div className="container">
                <div className="row">
                    <div className="col-2">
                        <div className="about-feed__box">
                            <h3> </h3>
                            <p>
                                 -    ,  ,  
                                     ,   
                                ,     ,  
                                -       .
                            </p>
                        </div>
                    </div>
                    <div className="col-2">
                        <div className="about-feed__box">
                            <img src={img} alt="img"/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default AboutFeed;